
.player-name {
  font-size: 16px;
  color: black;
}
.root {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 calc(20% - 16px);
          flex: 0 1 calc(20% - 16px);
  background-color: white;
  margin: 8px 0;
  border-radius: 0 5px 5px 0;
}
.root.right {
  border-radius: 5px 0 0 5px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.main {
  width: 220px;
}
.main .nameplate {
  text-align: center;
}
.main .nameplate .name {
  color: inherit;
  font-size: 32px;
}
.main .term-limit {
  text-align: center;
  padding: 3.2px;
  font-size: 18px;
}
.contents {
  position: relative;
}
.contents.right .card {
  -webkit-transform: none;
          transform: none;
}
.contents .card {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.contents .card.hide {
  opacity: 0;
}
.contents.dead {
  width: 0;
  opacity: 0;
}
.icon {
  font-size: 4rem;
}



